import React,{useState} from 'react'
import {Card} from 'react-bootstrap'
import styles from "../../../LeftNavigation/DesignerControls/BasicControls.module.css"
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import {faAngleDown, faAngleUp } from "@fortawesome/free-solid-svg-icons";
import ControlsMap from '../../../Properties/PropertiesConfiguration'
import ColorPicker from "../../../../ReusableComponents/ColorPicker/ColorPicker"
import ToggleBtn from "../../../../ReusableComponents/ToggleBtn/ToggleBtn"

function CollapsibleComponent(props){
   
    const [showCollapsibleBody, setShowCollapsibleBody] = useState(false);
    return (
        <Card className={styles.configuration1}>
                        <Card.Header className={styles.configurationHeader2} onClick={()=>setShowCollapsibleBody(!showCollapsibleBody)}>
                        <div className={styles.inputValidationsHeaderDiv } >
                            <span>{props.heading}</span>
                            {showCollapsibleBody === true ? <FontAwesomeIcon className={styles.downArrow} icon={faAngleUp}/>  : 
                            <FontAwesomeIcon className={styles.downArrow} icon={faAngleDown}/> }
                        </div>
                        </Card.Header>
        {showCollapsibleBody === true ? 
        <Card.Body className={  props.leftBody.length === 0 ? styles.emptyCardBody : styles.backdropCollapsibleBody}>
            <div className={styles.collapsibleLeftBody}>
            {props.leftBody.map((item)=>{
                return item;
            })}</div>
            <div className={styles.collapsibleRightBody}>
            {props.rightBody.map((item)=>{
                return item;
            })}
            </div>   
        </Card.Body>
        : null }
                        
        </Card> )
}

export default CollapsibleComponent