import React,{useEffect, useState} from 'react';
import Backdrop from '@material-ui/core/Backdrop';
import { makeStyles } from '@material-ui/core/styles';
import styles from "../../../LeftNavigation/DesignerControls/BasicControls.module.css"
import ControlsMap from '../../../Properties/PropertiesConfiguration'
import CollapsibleComponent from './CollapsibleComponentforBackdrop'
import ToggleBtn from "../../../../ReusableComponents/ToggleBtn/ToggleBtn"
import ColorPicker from "../../../../ReusableComponents/ColorPicker/ColorPicker"
import {Button} from 'react-bootstrap'
import { createUniqueId } from '../../IFormDesignerLogic';
import ColumnFromPageIcon from "../../../../Icons/ColumnFromPageIcon"
import  {ThemeJSON} from '../../Theme/ThemeJSON';
import axios from '../../../../axios'
import Grid from '@material-ui/core/Grid';
import SvgDeleteWhiteIcon from "../../../../Icons/DeleteWhiteIcon"
import TooltipMui from '@material-ui/core/Tooltip';
import { useTranslation } from 'react-i18next';



const useStyles = makeStyles((theme) => ({
 backdrop: {
 zIndex: 200,
 color: '#fff',
 },
}));
 
function CustomBackdrop(props) {
   

    const {t}=useTranslation();
    const classes = useStyles();

    const [error,setError]=useState(false);
    const[error1,setError1] = useState(false);
    const translationStyles=window?.appformBuilder_configs?.selectedLanguage === "ar" || window?.appformBuilder_configs?.selectedLanguage === "ar-SA";


    const [editId,setEditId]=useState("");
    // const [editCheck,setEditCheck]=useState(false);
    // const [indexOfProperty,setIndexOfProperty]=useState();
    // const [quesData,setQuesData] = useState([])

    const [customProperties,setCustomProperties]=useState(props.properties || []);
    
    // const [property,props.setProperty]=useState({
    //     name:"",
    //     value:""
    // });
 

    function save(){
        props.selectedControl.propertyList  = [...customProperties]; 
        props.setProperties(customProperties);
        props.setShowTableBackdrop(false);
        props.setIndexOfProperty();
        props.setEditCheck(false);
        props.setProperty({
            "name":"",
            "value":""
    });
    }
    
    function handleproperties()
    {
       
const {name , value} = props.property;
if(name==="" && value === ""){
setError(true);
setError1(true); 

}
else if(value === ""){
setError1(true)
}
else if(name==="" ){
setError(true);

}
else{
    setCustomProperties((prev)=>{
        return[
        ...prev,
       {
     name:props.property.name,
     value:props.property.value
       }

      ]});

        props.setProperty({
            "name":"",
            "value":""
    });
    setError(false); 
    setError1(false);

}
      
 
    }

    function modifyproperties(){
        var array = [...customProperties]; 
        array[props.indexOfProperty]=props.property;
        setCustomProperties(array);
    }

    function handleProperty(event){
        const {name,value}=event.target;
        if(name === "name"){
            if(value !== ""){
                setError(false)
                }
        }
        if(name === "value"){
        if(value !== ""){
            setError1(false)
            }
        }
            
            
        props.setProperty((prev)=>{
            return{
            ...prev, [name]:value
            }
        })
    }

    function deleteProperty(index){
          var array = [...customProperties]; 
          if (index !== -1) {
            array.splice(index, 1);
            setCustomProperties(array);
          }
    }


    function handlePropertyPart(index){
        props.setProperty(customProperties[index]);
        props.setIndexOfProperty(index);
        props.setEditCheck(true);
    }



    function handleClose(){  
       props.setShowTableBackdrop(false);
       props.setIndexOfProperty();
       props.setProperty({
        "name":"",
        "value":""
});
    }
    useEffect(()=>{
        document.getElementById("backdropDiv")?.focus();
        
       },[])
    function handleEscapeKey(e){
        if(e.code === "Escape"){
            handleClose()
        } 
       }
    
    
    return (
    <>
    <Backdrop id="backdrop" className={classes.backdrop} open={true} >
    <div tabIndex="0" onKeyDown={(e) => handleEscapeKey(e)} id="backdropDiv" className={styles.backdropDiv}>
        <div className={styles.backdropHeaderDiv}>
            <div className={styles.headerLeft}>{t("Add Properties")}</div>
            <div className={styles.headerRight}></div>
        </div>
        <hr className={styles.hrTagBackdrop}></hr>
        <div className={styles.backdropBodyDiv}>
            <div className={styles.backdropBodyLeftPart}>
                <div className={styles.backdropBodyLeftPartColumnListText}>{t("Properties Name")}</div>



                   { /*console.log("Object.values(customProperties)",Object.values(customProperties))*/}
                {Object.values(customProperties).map((item, index)=>{
                  
                        return (<>
                            <div className={props.indexOfProperty===index ? styles.backdropBodyLeftPartColumnListOddRowSelected : styles.backdropBodyLeftPartColumnListOddRow
                            } onClick={()=>handlePropertyPart(index)}> 
            
                            <span className={styles.backdropBodyLeftPartColumnListColumnName}>{item.name}</span>
                          </div></>
                        )
                      
                })}
                  
            </div>
            <div className={styles.backdopBodyRightPartSlider}>
            <div className={styles.backdropBodyRightPartColumnListTextSlider}>{t("Custom Properties")}</div>

            <div className={styles.backdropBodyRightPartColumnListTextSlider2}>

                 {t("Property Name")}
                 <div className={styles.inputSliderEmpty}>
                 <input aria-label={"Property Name"} className={!error ? styles.inputSlider:styles.inputError }  name="name" value={editId===""?props.property.name:""}  onChange={handleProperty}/>
                  <div>{!error ?  "":"This Field is required"}</div>
                 </div>
                 
                 {t("Property Value")}
                 <div className={styles.inputSliderEmpty}>
                 <input aria-label={"Property Value"} className={!error1 ?styles.inputSlider:styles.inputError} name="value" value={editId===""?props.property.value:""} onChange={handleProperty} />
                 <div>{!error1 ?  "":"This Field is required"}</div>
                 </div>
                 {props.editCheck ?  
                 <Button disableFocusRipple
                 disableRipple
                 tabIndex={0}
                   className={`formBuilder-MuiButton-outlined ${styles.addSliderButton}`}  onClick={modifyproperties} >{t("Modify")}</Button>
                 :
                 <Button disableFocusRipple
                 disableRipple
                 tabIndex={0}
                   className={`formBuilder-MuiButton-outlined ${styles.addSliderButton}`}  onClick={handleproperties} >{t("+ ADD")}</Button>
                }
            </div>
    
            <hr className={styles.hrTagBackdropSlider}></hr>


            <div className={styles.addedSlider} style={translationStyles ? {textAlign: "right"} : {}}>{t("Added Properties")}</div>

            <div className="options-grid_div">
            <Grid className={styles.optionsheading} item container >
                    <Grid item className={styles.optionscontentitems} style={{borderRight:"none"}} > 
                  {t("Property Name")}
                    </Grid>
                    <Grid item className={styles.optionscontentitems} style={{borderRight:"none"}} > 
                    {t("Property Value")}
                    </Grid>
                    <Grid item className={styles.optionscontentitemsaction}  >
                   {t("Action")}
                    </Grid>
                </Grid>

                {Object.values(customProperties).map((item,index)=>{
                    return(
                <Grid className={styles.optionsheading} item container >
                    <Grid item className={styles.optionsvalueitems} style={{borderRight:"none"}} > 
                    {item.name}
                    </Grid>
                    <Grid item className={styles.optionsvalueitems} style={{borderRight:"none"}} > 
                    {item.value}
                    </Grid>
                    <Grid item className={styles.optionsvalueitemsaction} onClick={()=>deleteProperty(index)}>
                    <SvgDeleteWhiteIcon />
                    </Grid>
                </Grid>
                    )})}
             </div>

            </div>
        </div>
        <div id="footer" className={styles.colButtonDiv} style={ translationStyles ? {display: 'flex', justifyContent: 'flex-end'} : {}}>
            <Button disableFocusRipple
                 disableRipple
                 tabIndex={0}
                   className={`formBuilder-MuiButton-outlined ${styles.saveAndExitButton}`} onClick={()=>save()} >{t("Save and Exit")}</Button>
            <Button disableFocusRipple
                 disableRipple
                 tabIndex={0}
                   className={`formBuilder-MuiButton-outlined ${styles.cancelBackdropButton}`} onClick={()=>handleClose()} >{t("Cancel")}</Button>
        </div>
    </div>
    
    </Backdrop>
    </>
    );
}
export default CustomBackdrop;